@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Inter', sans-serif;
}

body, html{
  background-color: rgb(43, 43, 43);
  color: white;
}

li{
  list-style: none;
}

label{
  cursor: pointer;
}

input[type='text']{
  padding: 0.3rem 0.5rem;
  border-radius: 5px;
  border: none;
}

input[type='checkbox']{
  cursor: pointer;
}

#root{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 2rem 2%;
}

.error{
  color: red;
  font-size: 30px;
}
/* Стили для списка данных */
.testApp{
  display: inline-block;
}

.itemList{
  margin-bottom: 2rem;
}

.itemList li{
  margin-bottom: 0.3rem;
}

.itemList li:last-child{
  margin-bottom: 0rem;
}

.itemList-name{
  color: rgb(0, 255, 85);
}
/* --- */


/* Стили для фильтра */
.filters{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom: 5rem;
  background-color: rgb(105, 105, 105);
  padding: 1rem;
  border-radius: 20px;
}

.filters div:not(.male, .female){
  margin-bottom: 0.5rem;
}

.filters label{
  margin-right: 0.3rem;
}

.filter div:last-child{
  margin-bottom: 0rem;
}

.filters-title{
  color: rgb(212, 145, 0);
  font-weight: bold;
  font-size: 20px;
  margin-bottom: 0.5rem;
}

.name, .lastname, .age, .sex{
  width: 100%;
  -webkit-align-self: flex-start;
          align-self: flex-start;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.sex .female{
  margin-right: 5rem;
}

.male label, .female label{
  margin-left: 0.2rem;
}
/* --- */
